/**
 * Type for notification. Can be one of "info", "success", "error", or "warning".
 */
export type SystemNotificationType = "info" | "success" | "error" | "warning";

/**
 * Interface for a notification entry.
 */
export interface SystemNotificationInterface {
  /**
   * Title of the notification.
   */
  title?: string;

  /**
   * The text content of the notification.
   */
  message?: string;

  /**
   * Duration for which the notification should be displayed.
   */
  duration?: number;

  /**
   * Indicates whether the notification can be closed manually.
   */
  closable?: boolean;

  /**
   * Is the notification closing.
   */
  closing?: boolean;

  /**
   * The type of the notification.
   */
  type?: SystemNotificationType;

  /**
   * The unique key for reusing of the component (for frameworks).
   */
  key?: string;
}
